.All-coding-Main-div{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  color: rgb(162, 225, 233);
  margin: 0;
  padding-bottom: 15%;
}
.All-coding-p{
  font-size: 26px;
  font-weight: 500;
  padding: 20px;
  padding-bottom: 40px;
}
@keyframes changeColor {
  0% {
    background-color: rgb(2, 89, 95);
  }
  100% {
    background-color: rgb(33, 103, 126);
  }
}
.Cbs-main-divs{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  animation: changeColor 5s infinite alternate;
  color: rgb(16, 227, 255);
  font-weight: 200;
}
